.Gallerie{
    .banniere{
        width: 100vw;
        height: 60vh;

        background-image: url(../../img/gallerie2.jpg);
        background-position: 10% 40%;
        background-repeat: no-repeat;
        background-size: cover;

        display: flex;
        flex-direction: column;
        justify-content: end;
        align-items: center;

        div{
            background-color: rgba(0, 0, 0, 0.4);
            color: white;
            width: 100vw;
            height: 60px;

            display: flex;
            justify-content: center;
            align-items: center;

            font-size: clamp(22px, 1.5vw, 30px);
        }
    }
    .titre{
        background-color: #DE9E36;
        color: white;
        height: 60px;
        width: 100vw;
        margin: 40px 0px;

        display: flex;
        justify-content: center;
        align-items: center;

        h2{
            font-family: Belleza;
            font-weight: 1000;
            font-size: clamp(25px, 2.3vw, 50px);
        }
    }
    .img{
        div{
            width: 100%;
            height: clamp(230px, 20vw, 500px);
            margin: 30px 0px;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        div:nth-of-type(1){
            background-image: url();
        }
    }
}
@media only screen and (min-width: 901px){
    .Gallerie{
        .banniere{
            width: 100vw;
            height: 80vh;

            background-position: 10% 40%;
            div{
                background-color: rgba(0, 0, 0, 0.4);
                color: white;
                width: 100vw;
                height: 80vh;
    
                display: flex;
                justify-content: center;
                align-items: center;
    
                font-size: clamp(40px, 4vw, 100px);
            }
        }
        .titre{
            height: 4vw;
        }
        .img{
            display: grid;
            grid-template-columns: 1fr 1fr;

            div{
                display: flex;
                justify-content: center;
                img{
                    width: 80%;
                }
            }
        }
    }
}